@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');

$small: 330px;

$big: 700px;

body{
  p,h1{
    margin: 0;
    font-family: 'roboto',sans-serif;
  }
}
// ===========Main Div and Backgound==========
.main{
margin: 8vh 0;
  height: 84vh;
  width: 100%;
  color: white;
}
.main_pc{
    width: 100%;
    height: 100vh;
    color: white;
    font-size: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    p{
      margin: 5vh 2vw;
    }
  }
.background{
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -1;
  background: linear-gradient(
    201.02deg, #000000 
    -8.63%, #9E00D6 
     49.79%, #000000 108.22%);
}

// ==============Header===================
.header{
  position: fixed;
  width: 100%;
  height: 8vh;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(90deg, #250C25 0%, #470548 100%);
    img{
      padding-left: 5%;
    }
}

// =============Footer===============
.footer{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 10vh;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  background-color: black;

  &_motto{
    display: flex;
    justify-content:center;
    align-items: center;
    text-align: center;
    color: white;
    transform: rotate(180deg);
    background: #1E0526;
    box-shadow: inset 0px 0px 4px 5px rgba(255, 255, 255, 0.25);
    width: 100%;
    height: 60%;
  }

  &_madeBy{
    display: flex;
    justify-content:center;
    color: white;
    background-color: black;
    width: 100%;
    height: 40%;
    font-size: 0.6em;

      &_madeByLogo{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &_madeByTeam{
        flex: 2;
        display: flex;
        flex-direction: column;
        text-align: center;
        &_by{
          padding-left: 20vw;
        }
      }
  }
}


// ========MENU (Navigation Button)================
.NavMenu{
padding: 1px;
}
.NavMenuBtn{
  margin-right: 20px;
  border-radius: 20%;
  width: 10vw;
  height: 5vh;
  display: flex;
  align-items: center;
  justify-content: center;
  }
.hamburger{
  transform: rotate(90deg);
}


// ======SIGN IN COMPONENT==============
.SignIn{
  width: 100%;
  height: 84vh;
  display: flex;
  flex-direction: column;
  color: white;
  font-family: 'Roboto', sans-serif;
font-weight: 300;
font-size: 1.5em;
  &_section_one{
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:space-around;
    text-align: center;
  }
  &_section_two{
    flex: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
&_please_sign_in,&_login,&_password,&_button{
  margin: 2vh 0;
}
&_login, &_password{
  text-align: center;
  border-radius: 10px;
  background: linear-gradient(90deg, #FEFEFE 0%, rgba(239, 238, 238, 0.65) 100%);
height: 5vh;
width: 70vw;
border: none;
box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}
&_button{
  border-radius: 10px;
  background: white;
  width: 40vw;
  height: 5vh;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}
&_error{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
}

// =============ITEM==================
.ItemConteiner{
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  background: #541D1D;
  height: 10vh;
  margin: .2vh 0;
  color: white;
  font-size:1em;
  @media only screen and (max-width: $small){
    font-size:0.8em;
  }
  @media only screen and (min-width: $big){
    font-size:1em;
  }
}
.ItemCode{
  flex: 1;
  height: 100%;
  text-align: left;
  background: linear-gradient(90.15deg, #65517E 0.1%, rgba(101, 81, 126, 0) 99.84%);
  display: flex;  
  flex-direction: column;
  &_button{
    flex: 2;
    width: 40%;
      border-radius: 5px;
      @media only screen and (max-width: $small){
        font-size:0.8em;
      }
      @media only screen and (min-width: $big){
        font-size:1em;
      }
    } 
    &_code{
      flex: 5;
      display: flex;
        border-radius: 5px;
        align-items: center;
        justify-content: center;
        
      } 
}
.ItemInfo{
  flex: 2;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content:center;
  border-left: 1px solid black;
  background: linear-gradient(270deg, #796692 13.54%, rgba(121, 102, 146, 0) 100%);

}

// =========TICKETS===================
.AllTickets{
  text-align: center;
  padding-top: 7vh;
padding-bottom: 12vh;
display: flex;
flex-direction: column;
@media only screen and (max-width: $small){
  font-size:0.8em;
}
@media only screen and (min-width: $big){
  font-size:1.5em;
}
}
.searchbox{
  position:fixed;
  display: flex;
  left: 0;
  top: 8vh;
  width: 100%;
  height: fit-content;
  background: purple;
  color: white;
  border-bottom: 1px solid #380839;
  &_search{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    &_input{
      width: 40vw;
      height: 3vh;
      border-radius: 5px;
      border: none;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      
    }
  }
  &_selector{
    flex: 1;  
    display: flex;
    flex-direction: column;
    align-items: space-between;
  }
}

// =============Skaner==============
.skaner{
width: 100%;
height: 100%;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
  &_qr_reader{
    flex: 1;
    padding-top: 2vh;
    width: 80vw;
    max-width: 300px;
    max-height: 300px;
    position: relative;
    @media only screen and (min-width: $big){
      max-width: 500px;
      max-height: 500px;
    }
  }
  &_scan_info{
    flex: 2;
    width: 100%;
    display: flex;
    align-items: center;
    &_scan_window{
      width: 100%;
      height: 60%;
      background: #242424;
      box-shadow: 0px 0px 4px #FFFFFF;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: space-around;
&_span{
  display: flex;
  align-items: center;
  justify-content: center;
}
    }
  }
  &_scan_button{
    flex: 1;
    color: red;
    &_btn{
      width: 50vw;

    }
  }
}
.scan_info{
  position: absolute;
  top: 8vh;
  left: 0%;
  width: 100%;
  height: 82vh;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  &_box{
    display: flex;
    flex-direction: column;
    width: 95%;
    height: 40%;
    align-items: center;
    // justify-content: center;
    border-radius: 10px;
    box-shadow: 0px 0px 4px 4px rgba(255, 255, 255, 0.5);
        &_name{
          font-size: 2em;
        }
        &_code{
          font-size: 1.4em;
          padding-bottom: 1vh;
        }
        &_status{
          font-size: 1em;
          padding-bottom: 1vh;
        }
        &_type{
          font-size: 1.5em;
          padding-bottom: 1vh;
        }
        &_quantity{
          font-size: 1.5em;
          padding-bottom: 1vh;
        }
  }
  &_box_red{
    // background: #841616 !important;
    background: linear-gradient(201.02deg, #000000 -8.63%, #FF0000 49.79%, #000000 108.22%) !important;
  }
  &_box_green{
    // background: green !important;
    background: linear-gradient(201.02deg, #000000 -8.63%, #24FF00 49.79%, #000000 108.22%) !important;

  }

}
.scan_info_green{
  background: rgba(0, 128, 0, 0.6);
}
.scan_info_red{
  background: rgba(132, 22, 22, 0.6)
  
}
// =================LOGOUT==============
.logout{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  &_window{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 95%;
    height: 35%;
    background: rgba(61, 60, 60, 0.80);
    box-shadow: 0px 0px 4px 2px rgba(255, 255, 255, 0.6);
    border-radius: 15px;
    &_text{
      padding-bottom: 5vh;
      font-size: 1.3em;
    }
    &_buttons{
      display: flex;
      width: 100%;
      justify-content: center;
    }
  }

}
// ============Ticket Detail==============
.TicketDetail{
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 30%;
  left: 2.5%;
  width: 95%;
  height: 50vh;
   background: rgba(61, 60, 60, 0.98);
    box-shadow: 0px 0px 4px 2px rgba(255, 255, 255, 0.6);
    border-radius: 15px;
    &_name{
    flex: 2;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    &_box{
      width: 80%;
      background: #2B1E3C;
      border: 1px solid #FFFFFF;
      box-shadow: 1px 4px 4px rgba(255, 255, 255, 0.25);
      border-radius: 5px;
    }
    }
    &_status{
      flex: 3;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      &_box{
        width: 95%;
        height: 90%;
        background: #841616;
        border: 1px solid #FFFFFF;
        box-shadow: 0px 3px 4px rgba(255, 255, 255, 0.5);
        border-radius: 5px;
        display: flex;
        align-items: center;
        &_col_1{
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          text-align: center;
          font-size: 0.9em;
        }
        &_col_2{
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          text-align: center;
          font-size: 0.9em;
        }

      
      }
    }
    &_promo{
      flex: 5;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      &_box{
        width: 95%;
        height: 90%;
        background: #5B4366;
        border: 1px solid #FFFFFF;
        box-shadow: 1px 4px 4px rgba(255, 255, 255, 0.25);
        border-radius: 5px;
        padding-top: 1vh;
      }
    }
    &_button{
      flex: 1;
    }
}