@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: grey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body p,body h1{margin:0;font-family:"roboto",sans-serif}.main{margin:8vh 0;height:84vh;width:100%;color:#fff}.main_pc{width:100%;height:100vh;color:#fff;font-size:2em;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center}.main_pc p{margin:5vh 2vw}.background{width:100vw;height:100vh;position:fixed;z-index:-1;background:linear-gradient(201.02deg, #000000 -8.63%, #9E00D6 49.79%, #000000 108.22%)}.header{position:fixed;width:100%;height:8vh;top:0;left:0;display:flex;align-items:center;justify-content:space-between;background:linear-gradient(90deg, #250C25 0%, #470548 100%)}.header img{padding-left:5%}.footer{position:fixed;bottom:0;left:0;width:100%;height:10vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#000}.footer_motto{display:flex;justify-content:center;align-items:center;text-align:center;color:#fff;transform:rotate(180deg);background:#1e0526;box-shadow:inset 0px 0px 4px 5px rgba(255,255,255,.25);width:100%;height:60%}.footer_madeBy{display:flex;justify-content:center;color:#fff;background-color:#000;width:100%;height:40%;font-size:.6em}.footer_madeBy_madeByLogo{flex:1 1;display:flex;align-items:center;justify-content:center}.footer_madeBy_madeByTeam{flex:2 1;display:flex;flex-direction:column;text-align:center}.footer_madeBy_madeByTeam_by{padding-left:20vw}.NavMenu{padding:1px}.NavMenuBtn{margin-right:20px;border-radius:20%;width:10vw;height:5vh;display:flex;align-items:center;justify-content:center}.hamburger{transform:rotate(90deg)}.SignIn{width:100%;height:84vh;display:flex;flex-direction:column;color:#fff;font-family:"Roboto",sans-serif;font-weight:300;font-size:1.5em}.SignIn_section_one{flex:2 1;display:flex;flex-direction:column;align-items:center;justify-content:space-around;text-align:center}.SignIn_section_two{flex:3 1;display:flex;flex-direction:column;align-items:center;justify-content:center}.SignIn_please_sign_in,.SignIn_login,.SignIn_password,.SignIn_button{margin:2vh 0}.SignIn_login,.SignIn_password{text-align:center;border-radius:10px;background:linear-gradient(90deg, #FEFEFE 0%, rgba(239, 238, 238, 0.65) 100%);height:5vh;width:70vw;border:none;box-shadow:2px 2px 2px rgba(0,0,0,.5)}.SignIn_button{border-radius:10px;background:#fff;width:40vw;height:5vh;box-shadow:2px 2px 2px rgba(0,0,0,.5)}.SignIn_error{display:flex;flex-direction:column;align-items:center;justify-content:center}.ItemConteiner{display:flex;align-items:center;justify-content:center;border:1px solid #000;background:#541d1d;height:10vh;margin:.2vh 0;color:#fff;font-size:1em}@media only screen and (max-width: 330px){.ItemConteiner{font-size:.8em}}@media only screen and (min-width: 700px){.ItemConteiner{font-size:1em}}.ItemCode{flex:1 1;height:100%;text-align:left;background:linear-gradient(90.15deg, #65517E 0.1%, rgba(101, 81, 126, 0) 99.84%);display:flex;flex-direction:column}.ItemCode_button{flex:2 1;width:40%;border-radius:5px}@media only screen and (max-width: 330px){.ItemCode_button{font-size:.8em}}@media only screen and (min-width: 700px){.ItemCode_button{font-size:1em}}.ItemCode_code{flex:5 1;display:flex;border-radius:5px;align-items:center;justify-content:center}.ItemInfo{flex:2 1;display:flex;flex-direction:column;width:100%;height:100%;align-items:center;justify-content:center;border-left:1px solid #000;background:linear-gradient(270deg, #796692 13.54%, rgba(121, 102, 146, 0) 100%)}.AllTickets{text-align:center;padding-top:7vh;padding-bottom:12vh;display:flex;flex-direction:column}@media only screen and (max-width: 330px){.AllTickets{font-size:.8em}}@media only screen and (min-width: 700px){.AllTickets{font-size:1.5em}}.searchbox{position:fixed;display:flex;left:0;top:8vh;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background:purple;color:#fff;border-bottom:1px solid #380839}.searchbox_search{flex:1 1;display:flex;align-items:center;justify-content:center}.searchbox_search_input{width:40vw;height:3vh;border-radius:5px;border:none;text-align:center;display:flex;align-items:center;justify-content:center}.searchbox_selector{flex:1 1;display:flex;flex-direction:column;align-items:space-between}.skaner{width:100%;height:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}.skaner_qr_reader{flex:1 1;padding-top:2vh;width:80vw;max-width:300px;max-height:300px;position:relative}@media only screen and (min-width: 700px){.skaner_qr_reader{max-width:500px;max-height:500px}}.skaner_scan_info{flex:2 1;width:100%;display:flex;align-items:center}.skaner_scan_info_scan_window{width:100%;height:60%;background:#242424;box-shadow:0px 0px 4px #fff;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.skaner_scan_info_scan_window_span{display:flex;align-items:center;justify-content:center}.skaner_scan_button{flex:1 1;color:red}.skaner_scan_button_btn{width:50vw}.scan_info{position:absolute;top:8vh;left:0%;width:100%;height:82vh;z-index:1000;display:flex;align-items:center;justify-content:center}.scan_info_box{display:flex;flex-direction:column;width:95%;height:40%;align-items:center;border-radius:10px;box-shadow:0px 0px 4px 4px rgba(255,255,255,.5)}.scan_info_box_name{font-size:2em}.scan_info_box_code{font-size:1.4em;padding-bottom:1vh}.scan_info_box_status{font-size:1em;padding-bottom:1vh}.scan_info_box_type{font-size:1.5em;padding-bottom:1vh}.scan_info_box_quantity{font-size:1.5em;padding-bottom:1vh}.scan_info_box_red{background:linear-gradient(201.02deg, #000000 -8.63%, #FF0000 49.79%, #000000 108.22%) !important}.scan_info_box_green{background:linear-gradient(201.02deg, #000000 -8.63%, #24FF00 49.79%, #000000 108.22%) !important}.scan_info_green{background:rgba(0,128,0,.6)}.scan_info_red{background:rgba(132,22,22,.6)}.logout{display:flex;align-items:center;justify-content:center;width:100%;height:100%}.logout_window{display:flex;flex-direction:column;align-items:center;justify-content:center;width:95%;height:35%;background:rgba(61,60,60,.8);box-shadow:0px 0px 4px 2px rgba(255,255,255,.6);border-radius:15px}.logout_window_text{padding-bottom:5vh;font-size:1.3em}.logout_window_buttons{display:flex;width:100%;justify-content:center}.TicketDetail{position:fixed;display:flex;flex-direction:column;align-items:center;justify-content:center;top:30%;left:2.5%;width:95%;height:50vh;background:rgba(61,60,60,.98);box-shadow:0px 0px 4px 2px rgba(255,255,255,.6);border-radius:15px}.TicketDetail_name{flex:2 1;width:100%;display:flex;align-items:center;justify-content:center}.TicketDetail_name_box{width:80%;background:#2b1e3c;border:1px solid #fff;box-shadow:1px 4px 4px rgba(255,255,255,.25);border-radius:5px}.TicketDetail_status{flex:3 1;width:100%;display:flex;align-items:center;justify-content:center}.TicketDetail_status_box{width:95%;height:90%;background:#841616;border:1px solid #fff;box-shadow:0px 3px 4px rgba(255,255,255,.5);border-radius:5px;display:flex;align-items:center}.TicketDetail_status_box_col_1{flex:1 1;display:flex;flex-direction:column;justify-content:center;text-align:center;font-size:.9em}.TicketDetail_status_box_col_2{flex:1 1;display:flex;flex-direction:column;justify-content:center;text-align:center;font-size:.9em}.TicketDetail_promo{flex:5 1;width:100%;display:flex;align-items:center;justify-content:center}.TicketDetail_promo_box{width:95%;height:90%;background:#5b4366;border:1px solid #fff;box-shadow:1px 4px 4px rgba(255,255,255,.25);border-radius:5px;padding-top:1vh}.TicketDetail_button{flex:1 1}
